.media {
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 5px 0;
    background-color: #282c34;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat", sans-serif;
  }
  
  .media:hover {
    background-color: white;
    color: red;
  }
  
  @media (max-width: 550px) {
    .media {
      width: 46%;
    }
  }
  
  .poster {
    border-radius: 10px;
  }
  
  .title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
    color: red;
  }
  
  .type{
    display: block;
    justify-content: space-between;
    padding: 0 2px;
    padding-bottom: 3px;
    padding-right: 25px;
    color: red;
    font-weight: bolder;
  
  }
  .badge{
    top:0;
    left: 285px;
  }